import React, { Component } from 'react';
import {Input, FormGroup, Label, CustomInput } from 'reactstrap';
import { toast} from 'react-toastify';
let countQuestion = 0;

const renderHTML = (rawHTML: string) => React.createElement("customlabel", { dangerouslySetInnerHTML: { __html: rawHTML } });
function FieldLayout(props) {

  const formFieldDetails = props.formFieldDetails;
  if(formFieldDetails === null || formFieldDetails === undefined) {
    return "";
  }
  countQuestion++;
  //props.formFieldName(formFieldDetails.id);
  switch(formFieldDetails.element) {
    case 'Header':
      return(
          <h2 className="form-heading-title">{renderHTML(formFieldDetails.content.trim())}</h2>
      )
     
    case 'TextInput':
      return(
        <div className="formField-item">
          <FormGroup>
            <Label className="formField-label" htmlFor={formFieldDetails.id}>{renderHTML(formFieldDetails.label.trim())}{formFieldDetails.required ? "*" : ""}</Label>
            <Input name={formFieldDetails.id} id={formFieldDetails.id} required={formFieldDetails.required ? true : false} placeholder={formFieldDetails.label} value = {props.formValue} disabled={props.disabledModule}   />
          </FormGroup>
        </div>
      )

    case 'DatePicker':
      return(
        <div className="formField-item">
          <FormGroup>
            <Label className="formField-label" htmlFor={formFieldDetails.id}>{renderHTML(formFieldDetails.label.trim())}{formFieldDetails.required ? "*" : ""}</Label>
            <Input name={formFieldDetails.id} type="date" id={formFieldDetails.id} required={formFieldDetails.required ? true : false} value = {props.formValue} disabled={props.disabledModule} placeholder={formFieldDetails.label}   />
          </FormGroup>
        </div>
      )
     
    case 'Checkboxes':
      return(
        <div className="formField-item">
          <FormGroup>
            <Label className="formField-label">{renderHTML(formFieldDetails.label.trim())}{formFieldDetails.required ? "*" : ""}</Label>
            <div>
              {formFieldDetails.options.map((checkBoxOptions, index) =>              
                <CustomInput name={formFieldDetails.id} key={index} disabled={props.disabledModule} type="checkbox" className="checkboxInput" label={checkBoxOptions.text} value={checkBoxOptions.value} id={checkBoxOptions.key} required={formFieldDetails.required ? true : false} placeholder={formFieldDetails.label}  />
              )}
            </div>
          </FormGroup>
        </div>
      )
     
    case 'TextArea':
      return(
        <div className="formField-item">
          <FormGroup>
            <Label className="formField-label" htmlFor={formFieldDetails.id}>{renderHTML(formFieldDetails.label.trim())}{formFieldDetails.required ? "*" : ""}</Label>
            <Input name={formFieldDetails.id} type="textarea" id={formFieldDetails.id} required={formFieldDetails.required ? true : false} placeholder={formFieldDetails.label}  value = {props.formValue} disabled={props.disabledModule}  />
          </FormGroup>
        </div>
      )
     
    case 'Dropdown':
      return(
        <div className="formField-item">
          <FormGroup>
            <Label className="formField-label">{renderHTML(formFieldDetails.label.trim())}{formFieldDetails.required ? "*" : ""}</Label>
            <Input name={formFieldDetails.id} type="select" className="dropDown" value = {props.formValue} id={formFieldDetails.id} required={formFieldDetails.required ? true : false} disabled={props.disabledModule} >

              {formFieldDetails.options.map((dropOptions, index) =>              
                <option value={dropOptions.value} key={index} >{dropOptions.text}</option>
              )}
            </Input>
          </FormGroup>
        </div>
      )
     
    case 'RadioButtons':
      
      return(
        <div className="inspection-form-card">
            <FormGroup>
              <Label className="inspection-title">{ renderHTML(formFieldDetails.label.trim())}{formFieldDetails.required ? "*" : ""}</Label>
              <div className="inspection-check">
                {formFieldDetails.options.map((radioButtonOptions, index) =>              
                  <CustomInput key={index} name={formFieldDetails.id} type="radio" disabled={props.disabledModule} checked={props.formValue === radioButtonOptions.value ? true : false} className="radioInput" label={radioButtonOptions.text} value={radioButtonOptions.value} id={radioButtonOptions.key} required={formFieldDetails.required ? true : false} placeholder={formFieldDetails.label}   />
                )}
              </div>
            </FormGroup>
            <div className="optional-form">
              {formFieldDetails.options.map((option, optionIndex) => 
               
                <div className="radio-optionform" key ={optionIndex}>
                  {props.formValue === option.value && option.enableAnotherFormField && option.moreOptions !== undefined ? <div className="option_form">
                    <p>{option.moreOptions.labelInfo}</p>
                    {option.moreOptions !== undefined ? option.moreOptions.formType === "tabularForm" ? 
                    <TabularForm disabledModule={props.disabledModule} data = {option.moreOptions.formData} clickEvent = {props.clickMoreTabularRows} clickRemoveEvent = {props.clickRemoveTabularRows} subparentElementId = {option.key} parentElementId = {formFieldDetails.id}  moreOptions numberofRows = {props.optionalData !== undefined && props.optionalData !== "" ? props.optionalData[option.key] !== undefined ? props.optionalData[option.key] : [{}] : [{}]} /> : 
                    option.moreOptions.formType === "tabularFormWithComponent" ? 
                    <TabularFormWithComponent disabledModule={props.disabledModule} data = {option.moreOptions.formData} clickEvent = {props.clickMoreTabularRows} clickRemoveEvent = {props.clickRemoveTabularRows} subparentElementId = {option.key} parentElementId = {formFieldDetails.id}  moreOptions numberofRows = {props.optionalData !== undefined && props.optionalData !== "" ? props.optionalData[option.key] !== undefined ? props.optionalData[option.key] : [{}] : [{}]} /> : option.moreOptions.formType === "singleInput" ? 
                    <SingleInput disabledModule={props.disabledModule} data = {option.moreOptions.formData} subparentElementId = {option.key} parentElementId = {formFieldDetails.id} optionalData = {props.optionalData !== undefined && props.optionalData !== "" ? props.optionalData[option.key] !== undefined ? props.optionalData[option.key] : {} : {}}  /> : 
                    option.moreOptions.formType === "questionComponent" ? 
                    <QuestionComponent disabledModule={props.disabledModule} data = {option.moreOptions.formData} subparentElementId = {option.key} parentElementId = {formFieldDetails.id} optionalData = {props.optionalData !== undefined && props.optionalData !== "" ? props.optionalData[option.key] !== undefined ? props.optionalData[option.key] : {} : {}}  /> : <></> : <></> } </div> : ""}
                </div> 
              )}
            </div>
            
                   
        </div>
      )
     
    case 'Name':
      let optionsEnbaled = formFieldDetails.options.filter(function(item) { return item.enableField;});
      return(
      <div className="formField-item">
        <div className="row">
        {optionsEnbaled.map((option, index) => {
            //const this_key = `preview_${option.key}`;
            const propsInput = {};
            propsInput.name = `${formFieldDetails.id}_optionalData__${option.key}`;

            propsInput.type = 'text';
            propsInput.value = option.value;
            const colName = Math.ceil(12 / optionsEnbaled.length);
            return (
              <div className={`col-md-${colName}`} key = {index}>
                <div className="form-group">
                  <label>{option.text}</label>
                  {option.fieldType !== "checkbox" &&  <input type={option.fieldType} disabled={props.disabledModule} name= {propsInput.name} className="form-control"  value = {props.optionalData !== undefined && props.optionalData[option.key] ? props.optionalData[option.key] :  ""} />
                  }
                  {option.fieldType === "checkbox" &&  <input type={option.fieldType} disabled={props.disabledModule} name= {propsInput.name} className="form-control"  checked = {props.optionalData !== undefined && props.optionalData[option.key] ? true: false} />
                  }
                  
                </div>
              </div>
            );
          })}
          </div>
        </div>
      )

    case 'Category':
      
      return(
      <div className="formField-item">
        { formFieldDetails.categoryDataInfo !== undefined ? 
            <div className="category-element-section">
              {formFieldDetails.categoryDataInfo.map((catInfo, catIndex) =>
                    <div className="category-element-row" key = {catIndex}>
                      <p>{catInfo.catName}</p>
                      <div className="form-group">
                        <label className="category-radio-label">
                          <input disabled={props.disabledModule} name={`${formFieldDetails.id}_optionalData__${catInfo.catId}`} type="radio" value="Yes" checked = {props.optionalData !== undefined && props.optionalData !== "" && props.optionalData[catInfo.catId] === "Yes" ? true : false}  /> Yes</label>
                        <label className="category-radio-label ml-2">
                            <input disabled={props.disabledModule} name={`${formFieldDetails.id}_optionalData__${catInfo.catId}`} checked = {props.optionalData !== undefined && props.optionalData !== "" && props.optionalData[catInfo.catId] === "No" ? true : false}  type="radio" value="No"  /> No</label>
                      </div>
                      <hr />   
                      {catInfo.subCategoryData.map((subCatInfo, subCatIndex) => 
                        <div className="category-element-row" key = {subCatIndex}>
                          <div className="row">
                            <label className="subcategory-radio-label col-md-4"><input name={`${formFieldDetails.id}_optionalData__${subCatInfo._id}`}  checked = {props.optionalData !== undefined && props.optionalData !== "" && props.optionalData[subCatInfo._id]  ? true : false}  type="checkbox" value={subCatInfo.name} /> {subCatInfo.name}</label>
                            <div className="form-group col-md-4">
                                <input disabled={props.disabledModule} type="text" className="form-control" name={`${formFieldDetails.id}_optionalData__${subCatInfo._id}_onset`} value = {props.optionalData !== undefined && props.optionalData !== "" && props.optionalData[subCatInfo._id+'_onset']  ? props.optionalData[subCatInfo._id+'_onset'] : ""}   placeholder="Onset"/>
                            </div>
                            <div className="form-group col-md-4">
                                <input disabled={props.disabledModule} type="date" dateformat="MM/DD/YYYY" name={`${formFieldDetails.id}_optionalData__${subCatInfo._id}_date`} value = {props.optionalData !== undefined && props.optionalData !== "" && props.optionalData[subCatInfo._id+'_date']  ? props.optionalData[subCatInfo._id+'_date'] : ""}  className="form-control" />
                            </div>
                          </div>
                        </div>
                      )}

                    </div>
                )}
            </div>
        : <></> }
      </div>
      )
    case 'Paragraph':
    
      return(
        <div className="formField-item">
          <p>{formFieldDetails.bold ?  <strong>{renderHTML(formFieldDetails.content.trim())}</strong>: renderHTML(formFieldDetails.content.trim())}</p>
        </div>
      )
     
    case 'Camera':
      return(
          <div className="formField-item">
          <FormGroup>
            <Label className="formField-label" htmlFor={formFieldDetails.id}>{formFieldDetails.label.trim()}{formFieldDetails.required ? "*" : ""}</Label>
            <Input name={formFieldDetails.id} disabled type="file" accept="image/*" id={formFieldDetails.id} required={formFieldDetails.required ? true : false} placeholder={formFieldDetails.label}   />
          </FormGroup>
        </div> 
      ) 
    case 'LineBreak':
      return (<hr />)
    default: 
      return (
        <div className="formField-item">
          <h2>{renderHTML(formFieldDetails.content)}</h2>
        </div>
      )
     

  }
} 

function SingleInput(props) {
  return (<div class="row">
    <div className="col-md-12">
      <div className="form-group">
          <label>{props.data.label} {props.data.isRequired ? <span className="required">*</span> : ""}</label>
          <input type = {props.data.type} disabled={props.disabledModule} value = {props.optionalData[props.data.id] !== undefined  ? props.optionalData[props.data.id] : ""} name={`${props.parentElementId}_optionalData__${props.subparentElementId}__${props.data.id}`}  />
      </div>
    </div>
  </div>) 
}
function TabularForm(props) {

  return (<div class="row">
    <div className="col-md-12">
      <table className="form_builder_table table">
        <thead>
          <tr>
              {props.data.columns.map((columnsInfo, colIndex) => 
                  <th key= {colIndex}>{columnsInfo.label}</th>
              )}
          </tr>
        </thead>
        <tbody>
          {
            props.numberofRows.map((rowsData, rowsIndex) => 
              <tr key = {rowsIndex}>
                {props.data.columns.map((columnsInfo, colIndex) => 
                      <td key= {colIndex}>
                        <div className="form-group">                            
                          {columnsInfo.type !== "checkbox" &&  <input disabled={props.disabledModule} type = {columnsInfo.type}   value = {rowsData[columnsInfo.id] || ""} name={`${props.parentElementId}_optionalData__${props.subparentElementId}__${rowsIndex}__${columnsInfo.id}`}   />
                          }
                          {columnsInfo.type === "checkbox" &&  <input disabled={props.disabledModule} type = {columnsInfo.type}  checked = {rowsData[columnsInfo.id] || false} value = {rowsData[columnsInfo.id] || ""} name={`${props.parentElementId}_optionalData__${props.subparentElementId}__${rowsIndex}__${columnsInfo.id}`}   />
                          }
                        </div>
                      </td>
                  )}
              </tr>
            )
          }
          
          
        </tbody>
      </table>
    </div>
    
  </div>) 
}
function TabularFormWithComponent(props) {
  return (<div class="row">
    <div className="col-md-12">
      <table className="form_builder_table table">
        <thead>
          <tr>
              {props.data.columns.map((columnsInfo, colIndex) => 
                  <th key= {colIndex}>{columnsInfo.label}
                  {columnsInfo.isSubForm ? <div class="subforminfo">
                    {columnsInfo.subFormData.map((subFormInfo, subIndex) => 
                      <span key = {subIndex}> <span>{subFormInfo.label}</span> / </span>
                    )}</div>
                  : <></> }
                  </th>
              )}
          </tr>
        </thead>
        <tbody>
          {
            props.numberofRows.map((rowsData, rowsIndex) => 
              <tr key = {rowsIndex}>
                {props.data.columns.map((columnsInfo, colIndex) => 
                      <td key= {colIndex}>
                        <div className="form-group">                            
                          {columnsInfo.type !== "checkbox" &&  <input disabled={props.disabledModule} type = {columnsInfo.type}   value = {rowsData[columnsInfo.id] || ""} name={`${props.parentElementId}_optionalData__${props.subparentElementId}__${rowsIndex}__${columnsInfo.id}`}   />
                          }
                          {columnsInfo.type === "checkbox" &&  <input disabled={props.disabledModule} type = {columnsInfo.type}  checked = {rowsData[columnsInfo.id] || false} value = {rowsData[columnsInfo.id] || ""} name={`${props.parentElementId}_optionalData__${props.subparentElementId}__${rowsIndex}__${columnsInfo.id}`}   />
                          }
                        </div>
                        {columnsInfo.isSubForm && <div class="subforminfo">
                          {columnsInfo.subFormData.map((subFormInfo, subIndex) => 
                            <div className="form-group">                            
                              {columnsInfo.type !== "checkbox" &&  <input disabled={props.disabledModule} type = {columnsInfo.type}   value = {rowsData[subFormInfo.id] || ""} name={`${props.parentElementId}_optionalData__${props.subparentElementId}__${rowsIndex}__${subFormInfo.id}`}   />
                              }
                              {columnsInfo.type === "checkbox" &&  <input disabled={props.disabledModule} type = {columnsInfo.type}  checked = {rowsData[subFormInfo.id] || false} value = {rowsData[subFormInfo.id] || ""} name={`${props.parentElementId}_optionalData__${props.subparentElementId}__${rowsIndex}__${subFormInfo.id}`}   />
                              }
                            </div>
                          )}</div>
                        }
                      </td>
                  )}
              </tr>
            )
          }
        </tbody>
      </table>
    </div>
    
  </div>) 
}

function QuestionComponent(props) {
  return (<div class="row">
    <div className="col-md-12">      
        {props.data.columns.map((columnsInfo, colIndex) => 
            <div className="row" key={colIndex}>
              <div className="col-md-6">
                {columnsInfo.label}
              </div>
              <div className="col-md-6">
                {(columnsInfo.type === "checkbox" || columnsInfo.type === "radio") && <div>
                  {columnsInfo.subFormData.map((subFormInfo, subIndex) => 
                    <div class="form-check form-check-inline" key = {subIndex}>
                      {columnsInfo.type === "checkbox" && <input disabled={props.disabledModule} class="form-check-input" type={columnsInfo.type} id={subFormInfo.id} name={`${props.parentElementId}_optionalData__${props.subparentElementId}__${subFormInfo.id}`} value={subFormInfo.label} checked = {props.optionalData[subFormInfo.id] !== undefined && props.optionalData[subFormInfo.id] === subFormInfo.label ? true : false}  />}
                      {columnsInfo.type === "radio" && <input disabled={props.disabledModule} class="form-check-input" type={columnsInfo.type} id={subFormInfo.id} name={`${props.parentElementId}_optionalData__${props.subparentElementId}__${columnsInfo.id}`} value={subFormInfo.label} checked = {props.optionalData[columnsInfo.id] !== undefined && props.optionalData[columnsInfo.id] === subFormInfo.label ? true : false}  /> }
                      <label class="form-check-label" for={subFormInfo.id}>{subFormInfo.label}</label>
                    </div>
                  )}
                  </div>
                }
                {columnsInfo.type === "dropdown"  && <Input disabled={props.disabledModule} type ="select" className="form-control" value = {props.optionalData[columnsInfo.id] !== undefined  ? props.optionalData[columnsInfo.id] : ""} name={`${props.parentElementId}_optionalData__${props.subparentElementId}__${columnsInfo.id}`} >
                  {columnsInfo.subFormData.map((subFormInfo, subIndex) => 
                      <option value={subFormInfo.label} key = {subIndex} >{subFormInfo.label}</option>
                  )}
                </Input>}
                {(columnsInfo.type === "text" || columnsInfo.type === "number" || columnsInfo.type === "date") && 
                <input type = {columnsInfo.type} disabled={props.disabledModule} className="form-control" value = {props.optionalData[columnsInfo.id] !== undefined  ? props.optionalData[columnsInfo.id] : ""} name={`${props.parentElementId}_optionalData__${props.subparentElementId}__${columnsInfo.id}`}  />}
              </div>
            </div>
        )}
          
    </div>
  </div>)
}

class FeedBackPreviewPageForm extends Component {
  
  constructor(props){
    super(props);   
    this.state = {
      buttonProcessing: false,
      rowIndex: '',
      dataTableItem: [],
      formFieldRemarks: {},
      formFieldValueRemarks: {},
      mediaFileData: {},
      inspectionId: "",
      modal: false,
      actionData: {}
    };
    
  }
  componentDidMount() {   
    
    
  }

  /* Update Counter On Each */
  componentDidUpdate(){
    
    //this.props.updateQuestionCountval(countQuestion);
  }
  
  
  render() {
    const formFiled = this.props.templateField;
    countQuestion = this.props.countQuestion;
   
    return (
      <>
      <div className="inspection-form-section">
         {formFiled.map((formFieldDetails, index) =>
            formFieldDetails === null || formFieldDetails === undefined ? "" : 
            <FieldLayout key={index} indexItem = {index} formFieldDetails={formFieldDetails} formFieldName = {this.props.createFormFieldName} 
            formValue = {this.props.formField[formFieldDetails.id]}
            optionalData = {this.props.formField[formFieldDetails.id+'_optionalData'] || ""}
            optionalDataInput = {this.props.optionalDataInput}
            disabledModule={true}   />
          )}
          
      </div>
      
      </>
    );
  }
}

