import React from "react";
import { Card, CardBody, CardHeader, Col, Row, FormGroup, Label, Tooltip} from 'reactstrap';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import commonService from '../../../core/services/commonService';
import commonFunction from '../../../core/functions/commonFunction';
import Loader from '../../Loader/Loader';
import FeedBackPreviewPageForm from '../../AdminDashboard/Assignment/FeedBackPreviewPageForm';
import Doc from '../../FormBuilder/DocService';
import PdfContainer from '../../FormBuilder/PdfContainer';
import "../../../assets/css/font-awesome.min.css";
import "../../AdminDashboard/Assignment/FeedbackPreview.css";
class ConsentFormPreview extends React.Component {
  constructor(props){
    super(props);
    this.state = {   
      loading: false,  
      feedBackId: "",     
      formProccessing: false,
      feedBackInfo: {templateFormData: [], feedBackData: {}, inspectionId: ""},
      templatePreviewData: [],
      formField:{},
      apiUrl: "",
      tooltipOpen: false
        
    }  
    this.toggleScoreTooltip = this.toggleScoreTooltip.bind(this);
   
  }

  // Fetch the subCategory List
  componentDidMount() {
    const { match: { params } } = this.props;
    
    if(params.profileId !== undefined && params.profileId !=="") 
      this.getInspectionFeedBackDetail(params.profileId);
    else
      this.props.history.push('/login'); 
    const apiUrl = commonService.getAPIUrl();
    this.setState({apiUrl: apiUrl});
    
  }

  toggleScoreTooltip(){
    
    this.setState({tooltipOpen: !this.state.tooltipOpen});
  }

  getInspectionFeedBackDetail(feedBackId) {
    this.setState( { loading: true}, () => {
      commonService.getAPIWithAccessToken('profile/feedback/'+feedBackId+'?formType=consent')
        .then( res => {         
          
          if ( undefined === res.data.data || !res.data.status ) {
            this.setState( {  loading: false } );
            toast.error(res.data.message);  
            this.props.history.push('/login');  
            return;
          } 
          const feedBackDetail = res.data.data;
          const inspectionDetail = res.data.data;          
          let formField = this.state.formField;      
          let mediaFileInfo = {};
          let prevMediaFileInfo = {};
          
          
          let feedbackDataId = "";
          let feedbackDataIdDetails = {};
          let templatePreviewData = [];

          feedbackDataIdDetails = inspectionDetail.feedBackDetails;
          templatePreviewData = inspectionDetail.templateInfo.formField || [];
          feedbackDataId = Object.keys(inspectionDetail.feedBackDetails).length > 0 ? feedbackDataIdDetails._id : "";
          if(Object.keys(feedbackDataIdDetails).length > 0 ) {
            for (var key in feedbackDataIdDetails.feedBackData) {
              formField[key] = feedbackDataIdDetails.feedBackData[key];
            }
          }
          let isDisabled = this.state.isDisabled;
          let autoTimerStart = this.state.autoTimerStart;
          /*if(inspectionDetail.consentFormStatus)
            isDisabled = true;*/
          if(!isDisabled)
            autoTimerStart = true;
          this.setState({loading:false, 
            loadingTemplate: true, 
            formField: formField, 
            formValid: true, 
            profileId: inspectionDetail.profileId, 
            authId: inspectionDetail.authId,
            totalFormFillingTime: inspectionDetail.totalFormFillingTime, 
            autoTimerStart: autoTimerStart, 
            isDisabled: isDisabled, 
            templateId: inspectionDetail.templateInfo.templateId || "", 
            templatePreviewData: templatePreviewData, 
            previousFeedBackData: inspectionDetail.feedBackData, 
            feedbackDataId: feedbackDataId,             
            mediaFileInfo: mediaFileInfo, 
            previousUploadedFile: prevMediaFileInfo});
              
          this.setState({loading:false, feedBackInfo: feedBackDetail});     
         
        } )
        .catch( err => {                   
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else {
            this.setState( { loading: false } );
            toast.error(err.message);    
          }
        } )
    } ) 
  }
  
  createPdf = (html) => { 
    window.postMessage(JSON.stringify({pdfUrl: this.state.feedBackInfo.downloadInspectionFeedBack}), '*');
    Doc.createPdf(html, this.state.feedBackInfo.templateName+'_'+commonService.getCurrentDate()); 
  }
  render() {
    const { loading, feedBackInfo} = this.state;     
    let loaderElement ='';
    const formFieldItem =  this.state.templatePreviewData;
    
    if(loading) {
      loaderElement = <Loader />
       return (
          <div className="animated fadeIn">
            <Row>
              <Col md={12}>
                <Card>
                  <CardHeader className="mainHeading">
                    <strong>Consent Form Preview </strong>
                  </CardHeader>
                  <CardBody>
                    {loaderElement}
                    
                  </CardBody>
                </Card>
              </Col>  
            </Row>
            
          </div>
        );
    }
    
    return (
      <div className="animated fadeIn">
        <Row>
          <Col md={12}>
            <Card>
              <CardHeader className="mainHeading">
                <strong>Preview</strong>
              </CardHeader>
              <CardBody className="feedBackPreview">
                {loaderElement}
                <PdfContainer createPdf={this.createPdf} hideShareButton = "true" showLogo="false" templateType = "inspection" inspectionId = {this.state.feedBackInfo.inspectionId} > 

                  <div className="card-Preview-info"> 
                    <Row>
                      <Col md={12}>
                        <Row>                        
                          <Col md={3}>
                            <FormGroup> 
                              <Label htmlFor="employeeId">First Name</Label>            
                              <p>{feedBackInfo.firstName ? feedBackInfo.firstName : ''}</p>
                            </FormGroup>
                          </Col>
                          <Col md={3}>
                            <FormGroup> 
                              <Label htmlFor="employeeId">Last Name</Label>            
                              <p>{feedBackInfo.lastName ? feedBackInfo.lastName : ''}</p>
                            </FormGroup>
                          </Col>  
                          
                          <Col md={6}>
                            <FormGroup> 
                              <Label htmlFor="score">Address</Label>             
                              <p>{feedBackInfo.address || ""}</p>
                            </FormGroup>
                          </Col>
                          <Col md={3}>
                            <FormGroup> 
                              <Label htmlFor="score">State</Label>             
                              <p>{feedBackInfo.state || ""}</p>
                            </FormGroup>
                          </Col>  
                                          
                        </Row>  
                      </Col>                    
                    </Row>
                  </div>             
                  <FeedBackPreviewPageForm 
                    profileId={this.state.profileId} 
                    templateField = {formFieldItem} 
                    formField={this.state.formField} 
                    countQuestion = {this.state.countQuestion} 
                    
                    disabledModule={this.state.isDisabled}  />
                </PdfContainer>
              </CardBody>
            </Card>
          </Col>  
        </Row>
        
      </div>
    );
  }
}

export default ConsentFormPreview;
