import React, { Component } from 'react';
import {Input, FormGroup, Label, CustomInput } from 'reactstrap';
import { toast} from 'react-toastify';
let countQuestion = 0;

const renderHTML = (rawHTML: string) => React.createElement("customlabel", { dangerouslySetInnerHTML: { __html: rawHTML } });
function FieldLayout(props) {

  const formFieldDetails = props.formFieldDetails;
  if(formFieldDetails === null || formFieldDetails === undefined) {
    return "";
  }
  countQuestion++;
  //props.formFieldName(formFieldDetails.id);
  switch(formFieldDetails.element) {
    case 'Header':
      return(
          <h2 className="form-heading-title">{renderHTML(formFieldDetails.content.trim())}</h2>
      )
     
    case 'TextInput':
      return(
        <div className="formField-item">
          <FormGroup>
            <Label className="formField-label" htmlFor={formFieldDetails.id}>{renderHTML(formFieldDetails.label.trim())}{formFieldDetails.required ? "*" : ""}</Label>
            <Input name={formFieldDetails.id} id={formFieldDetails.id} required={formFieldDetails.required ? true : false} placeholder={formFieldDetails.label} value = {props.formValue} onChange={props.onchangeEvent}  />
          </FormGroup>
        </div>
      )

    case 'DatePicker':
      return(
        <div className="formField-item">
          <FormGroup>
            <Label className="formField-label" htmlFor={formFieldDetails.id}>{renderHTML(formFieldDetails.label.trim())}{formFieldDetails.required ? "*" : ""}</Label>
            <Input name={formFieldDetails.id} type="date" id={formFieldDetails.id} required={formFieldDetails.required ? true : false} value = {props.formValue} placeholder={formFieldDetails.label} onChange={props.onchangeEvent}  />
          </FormGroup>
        </div>
      )
     
    case 'Checkboxes':
      return(
        <div className="formField-item">
          <FormGroup>
            <Label className="formField-label">{renderHTML(formFieldDetails.label.trim())}{formFieldDetails.required ? "*" : ""}</Label>
            <div>
              {formFieldDetails.options.map((checkBoxOptions, index) =>              
                <CustomInput name={formFieldDetails.id} key={index} type="checkbox" className="checkboxInput" label={checkBoxOptions.text} value={checkBoxOptions.value} id={checkBoxOptions.key} required={formFieldDetails.required ? true : false} placeholder={formFieldDetails.label} onChange={props.onchangeEvent} />
              )}
            </div>
          </FormGroup>
        </div>
      )
     
    case 'TextArea':
      return(
        <div className="formField-item">
          <FormGroup>
            <Label className="formField-label" htmlFor={formFieldDetails.id}>{renderHTML(formFieldDetails.label.trim())}{formFieldDetails.required ? "*" : ""}</Label>
            <Input name={formFieldDetails.id} type="textarea" id={formFieldDetails.id} required={formFieldDetails.required ? true : false} placeholder={formFieldDetails.label} onChange={props.onchangeEvent} value = {props.formValue}  />
          </FormGroup>
        </div>
      )
     
    case 'Dropdown':
      return(
        <div className="formField-item">
          <FormGroup>
            <Label className="formField-label">{renderHTML(formFieldDetails.label.trim())}{formFieldDetails.required ? "*" : ""}</Label>
            <Input name={formFieldDetails.id} type="select" className="dropDown" value = {props.formValue} id={formFieldDetails.id} required={formFieldDetails.required ? true : false} onChange={props.onchangeEvent}>

              {formFieldDetails.options.map((dropOptions, index) =>              
                <option value={dropOptions.value} key={index} >{dropOptions.text}</option>
              )}
            </Input>
          </FormGroup>
        </div>
      )
     
    case 'RadioButtons':
      
      return(
        <div className="inspection-form-card">
            <FormGroup>
              <Label className="inspection-title">{ renderHTML(formFieldDetails.label.trim())}{formFieldDetails.required ? "*" : ""}</Label>
              <div className="inspection-check">
                {formFieldDetails.options.map((radioButtonOptions, index) =>              
                  <CustomInput key={index} name={formFieldDetails.id} type="radio" disabled={props.disabledModule} checked={props.formValue === radioButtonOptions.value ? true : false} className="radioInput" label={radioButtonOptions.text} value={radioButtonOptions.value} id={radioButtonOptions.key} required={formFieldDetails.required ? true : false} placeholder={formFieldDetails.label} onChange={props.onchangeEvent}  />
                )}
              </div>
            </FormGroup>
            <div className="optional-form">
              {formFieldDetails.options.map((option, optionIndex) => 
               
                <div className="radio-optionform" key ={optionIndex}>
                  {props.formValue === option.value && option.enableAnotherFormField && option.moreOptions !== undefined ? <div className="option_form">
                    <p>{option.moreOptions.labelInfo}</p>
                    {option.moreOptions !== undefined ? option.moreOptions.formType === "tabularForm" ? 
                    <TabularForm data = {option.moreOptions.formData} clickEvent = {props.clickMoreTabularRows} clickRemoveEvent = {props.clickRemoveTabularRows} subparentElementId = {option.key} parentElementId = {formFieldDetails.id} changeEventWithMultiDimenssional = {props.onchangeEventWithMultiDimenssional} moreOptions numberofRows = {props.optionalData !== undefined && props.optionalData !== "" ? props.optionalData[option.key] !== undefined ? props.optionalData[option.key] : [{}] : [{}]} /> : 
                    option.moreOptions.formType === "tabularFormWithComponent" ? 
                    <TabularFormWithComponent data = {option.moreOptions.formData} clickEvent = {props.clickMoreTabularRows} clickRemoveEvent = {props.clickRemoveTabularRows} subparentElementId = {option.key} parentElementId = {formFieldDetails.id} changeEventWithMultiDimenssional = {props.onchangeEventWithMultiDimenssional} moreOptions numberofRows = {props.optionalData !== undefined && props.optionalData !== "" ? props.optionalData[option.key] !== undefined ? props.optionalData[option.key] : [{}] : [{}]} /> : option.moreOptions.formType === "singleInput" ? 
                    <SingleInput data = {option.moreOptions.formData} subparentElementId = {option.key} parentElementId = {formFieldDetails.id} optionalData = {props.optionalData !== undefined && props.optionalData !== "" ? props.optionalData[option.key] !== undefined ? props.optionalData[option.key] : {} : {}} changeQuestionInput = {props.onchangeQuestionInput} /> : 
                    option.moreOptions.formType === "questionComponent" ? 
                    <QuestionComponent data = {option.moreOptions.formData} subparentElementId = {option.key} parentElementId = {formFieldDetails.id} optionalData = {props.optionalData !== undefined && props.optionalData !== "" ? props.optionalData[option.key] !== undefined ? props.optionalData[option.key] : {} : {}} changeQuestionInput = {props.onchangeQuestionInput} /> : <></> : <></> } </div> : ""}
                </div> 
              )}
            </div>
            
                   
        </div>
      )
     
    case 'Name':
      let optionsEnbaled = formFieldDetails.options.filter(function(item) { return item.enableField;});
      return(
      <div className="formField-item">
        <div className="row">
        {optionsEnbaled.map((option, index) => {
            //const this_key = `preview_${option.key}`;
            const propsInput = {};
            propsInput.name = `${formFieldDetails.id}_optionalData__${option.key}`;

            propsInput.type = 'text';
            propsInput.value = option.value;
            const colName = Math.ceil(12 / optionsEnbaled.length);
            return (
              <div className={`col-md-${colName}`} key = {index}>
                <div className="form-group">
                  <label>{option.text}</label>
                  {option.fieldType !== "checkbox" &&  <input type={option.fieldType} name= {propsInput.name} className="form-control" onChange={props.optionalDataInput} value = {props.optionalData !== undefined && props.optionalData[option.key] ? props.optionalData[option.key] :  ""} />
                  }
                  {option.fieldType === "checkbox" &&  <input type={option.fieldType} name= {propsInput.name} className="form-control" onChange={props.optionalDataInput} checked = {props.optionalData !== undefined && props.optionalData[option.key] ? true: false} />
                  }
                  
                </div>
              </div>
            );
          })}
          </div>
        </div>
      )

    case 'Category':
      
      return(
      <div className="formField-item">
        { formFieldDetails.categoryDataInfo !== undefined ? 
            <div className="category-element-section">
              {formFieldDetails.categoryDataInfo.map((catInfo, catIndex) =>
                    <div className="category-element-row" key = {catIndex}>
                      <p>{catInfo.catName}</p>
                      <div className="form-group">
                        <label className="category-radio-label">
                          <input name={`${formFieldDetails.id}_optionalData__${catInfo.catId}`} type="radio" value="Yes" checked = {props.optionalData !== undefined && props.optionalData !== "" && props.optionalData[catInfo.catId] === "Yes" ? true : false} onChange={props.optionalDataInput} /> Yes</label>
                        <label className="category-radio-label ml-2">
                            <input name={`${formFieldDetails.id}_optionalData__${catInfo.catId}`} checked = {props.optionalData !== undefined && props.optionalData !== "" && props.optionalData[catInfo.catId] === "No" ? true : false} onChange={props.optionalDataInput} type="radio" value="No"  /> No</label>
                      </div>
                      <hr />   
                      {catInfo.subCategoryData.map((subCatInfo, subCatIndex) => 
                        <div className="category-element-row" key = {subCatIndex}>
                          <div className="row">
                            <label className="subcategory-radio-label col-md-4"><input name={`${formFieldDetails.id}_optionalData__${subCatInfo._id}`} onChange={props.optionalDataInput} checked = {props.optionalData !== undefined && props.optionalData !== "" && props.optionalData[subCatInfo._id]  ? true : false}  type="checkbox" value={subCatInfo.name} /> {subCatInfo.name}</label>
                            <div className="form-group col-md-4">
                                <input type="text" className="form-control" name={`${formFieldDetails.id}_optionalData__${subCatInfo._id}_onset`} value = {props.optionalData !== undefined && props.optionalData !== "" && props.optionalData[subCatInfo._id+'_onset']  ? props.optionalData[subCatInfo._id+'_onset'] : ""}  onChange={props.optionalDataInput} placeholder="Onset"/>
                            </div>
                            <div className="form-group col-md-4">
                                <input type="date" dateformat="MM/DD/YYYY" name={`${formFieldDetails.id}_optionalData__${subCatInfo._id}_date`} value = {props.optionalData !== undefined && props.optionalData !== "" && props.optionalData[subCatInfo._id+'_date']  ? props.optionalData[subCatInfo._id+'_date'] : ""} onChange={props.optionalDataInput} className="form-control" />
                            </div>
                          </div>
                        </div>
                      )}

                    </div>
                )}
            </div>
        : <></> }
      </div>
      )
    case 'Paragraph':
    
      return(
        <div className="formField-item">
          <p>{formFieldDetails.bold ?  <strong>{renderHTML(formFieldDetails.content.trim())}</strong>: renderHTML(formFieldDetails.content.trim())}</p>
        </div>
      )
     
    case 'Camera':
      return(
          <div className="formField-item">
          <FormGroup>
            <Label className="formField-label" htmlFor={formFieldDetails.id}>{formFieldDetails.label.trim()}{formFieldDetails.required ? "*" : ""}</Label>
            <Input name={formFieldDetails.id} disabled type="file" accept="image/*" id={formFieldDetails.id} required={formFieldDetails.required ? true : false} placeholder={formFieldDetails.label} onChange={props.onchangeFileEvent}  />
          </FormGroup>
        </div> 
      ) 
    case 'LineBreak':
      return (<hr />)
    default: 
      return (
        <div className="formField-item">
          <h2>{renderHTML(formFieldDetails.content)}</h2>
        </div>
      )
     

  }
} 

function SingleInput(props) {
  return (<div class="row">
    <div className="col-md-12">
      <div className="form-group">
          <label>{props.data.label} {props.data.isRequired ? <span className="required">*</span> : ""}</label>
          <input type = {props.data.type} value = {props.optionalData[props.data.id] !== undefined  ? props.optionalData[props.data.id] : ""} name={`${props.parentElementId}_optionalData__${props.subparentElementId}__${props.data.id}`} onChange = {props.changeQuestionInput} />
      </div>
    </div>
  </div>) 
}
function TabularForm(props) {

  return (<div class="row">
    <div className="col-md-12">
      <table className="form_builder_table table">
        <thead>
          <tr>
              {props.data.columns.map((columnsInfo, colIndex) => 
                  <th key= {colIndex}>{columnsInfo.label}</th>
              )}
          </tr>
        </thead>
        <tbody>
          {
            props.numberofRows.map((rowsData, rowsIndex) => 
              <tr key = {rowsIndex}>
                {props.data.columns.map((columnsInfo, colIndex) => 
                      <td key= {colIndex}>
                        <div className="form-group">                            
                          {columnsInfo.type !== "checkbox" &&  <input type = {columnsInfo.type}   value = {rowsData[columnsInfo.id] || ""} name={`${props.parentElementId}_optionalData__${props.subparentElementId}__${rowsIndex}__${columnsInfo.id}`} onChange={props.changeEventWithMultiDimenssional}  />
                          }
                          {columnsInfo.type === "checkbox" &&  <input type = {columnsInfo.type}  checked = {rowsData[columnsInfo.id] || false} value = {rowsData[columnsInfo.id] || ""} name={`${props.parentElementId}_optionalData__${props.subparentElementId}__${rowsIndex}__${columnsInfo.id}`} onChange={props.changeEventWithMultiDimenssional}  />
                          }
                        </div>
                      </td>
                  )}
              {rowsIndex !== 0 && <button className="remove-more-item" onClick = {(e) => props.clickRemoveEvent(props.parentElementId, props.subparentElementId, rowsIndex)}><i className="fa fa-times"></i></button> }
              </tr>
            )
          }
          
          
        </tbody>
      </table>
    </div>
    {props.data.isMultipleRow && 
        <div className ="col-md-3"><button className="add-more" onClick = {(e) => props.clickEvent(props.parentElementId, props.subparentElementId, e)}><i className="fa fa-plus"></i></button></div>
    }
  </div>) 
}
function TabularFormWithComponent(props) {
  return (<div class="row">
    <div className="col-md-12">
      <table className="form_builder_table table">
        <thead>
          <tr>
              {props.data.columns.map((columnsInfo, colIndex) => 
                  <th key= {colIndex}>{columnsInfo.label}
                  {columnsInfo.isSubForm ? <div class="subforminfo">
                    {columnsInfo.subFormData.map((subFormInfo, subIndex) => 
                      <span key = {subIndex}> <span>{subFormInfo.label}</span> / </span>
                    )}</div>
                  : <></> }
                  </th>
              )}
          </tr>
        </thead>
        <tbody>
          {
            props.numberofRows.map((rowsData, rowsIndex) => 
              <tr key = {rowsIndex}>
                {props.data.columns.map((columnsInfo, colIndex) => 
                      <td key= {colIndex}>
                        <div className="form-group">                            
                          {columnsInfo.type !== "checkbox" &&  <input type = {columnsInfo.type}   value = {rowsData[columnsInfo.id] || ""} name={`${props.parentElementId}_optionalData__${props.subparentElementId}__${rowsIndex}__${columnsInfo.id}`} onChange={props.changeEventWithMultiDimenssional}  />
                          }
                          {columnsInfo.type === "checkbox" &&  <input type = {columnsInfo.type}  checked = {rowsData[columnsInfo.id] || false} value = {rowsData[columnsInfo.id] || ""} name={`${props.parentElementId}_optionalData__${props.subparentElementId}__${rowsIndex}__${columnsInfo.id}`} onChange={props.changeEventWithMultiDimenssional}  />
                          }
                        </div>
                        {columnsInfo.isSubForm && <div class="subforminfo">
                          {columnsInfo.subFormData.map((subFormInfo, subIndex) => 
                            <div className="form-group">                            
                              {columnsInfo.type !== "checkbox" &&  <input type = {columnsInfo.type}   value = {rowsData[subFormInfo.id] || ""} name={`${props.parentElementId}_optionalData__${props.subparentElementId}__${rowsIndex}__${subFormInfo.id}`} onChange={props.changeEventWithMultiDimenssional}  />
                              }
                              {columnsInfo.type === "checkbox" &&  <input type = {columnsInfo.type}  checked = {rowsData[subFormInfo.id] || false} value = {rowsData[subFormInfo.id] || ""} name={`${props.parentElementId}_optionalData__${props.subparentElementId}__${rowsIndex}__${subFormInfo.id}`} onChange={props.changeEventWithMultiDimenssional}  />
                              }
                            </div>
                          )}</div>
                        }
                      </td>
                  )}
              {rowsIndex !== 0 && <button className="remove-more-item" onClick = {(e) => props.clickRemoveEvent(props.parentElementId, props.subparentElementId, rowsIndex)}><i className="fa fa-times"></i></button> }
              </tr>
            )
          }
        </tbody>
      </table>
    </div>
    {props.data.isMultipleRow && 
        <div className ="col-md-3"><button className="add-more" onClick = {(e) => props.clickEvent(props.parentElementId, props.subparentElementId, e)}><i className="fa fa-plus"></i></button></div>
    }
  </div>) 
}

function QuestionComponent(props) {
  return (<div class="row">
    <div className="col-md-12">      
        {props.data.columns.map((columnsInfo, colIndex) => 
            <div className="row" key={colIndex}>
              <div className="col-md-6">
                {columnsInfo.label}
              </div>
              <div className="col-md-6">
                {(columnsInfo.type === "checkbox" || columnsInfo.type === "radio") && <div>
                  {columnsInfo.subFormData.map((subFormInfo, subIndex) => 
                    <div class="form-check form-check-inline" key = {subIndex}>
                      {columnsInfo.type === "checkbox" && <input class="form-check-input" type={columnsInfo.type} id={subFormInfo.id} name={`${props.parentElementId}_optionalData__${props.subparentElementId}__${subFormInfo.id}`} value={subFormInfo.label} checked = {props.optionalData[subFormInfo.id] !== undefined && props.optionalData[subFormInfo.id] === subFormInfo.label ? true : false} onChange = {props.changeQuestionInput} />}
                      {columnsInfo.type === "radio" && <input class="form-check-input" type={columnsInfo.type} id={subFormInfo.id} name={`${props.parentElementId}_optionalData__${props.subparentElementId}__${columnsInfo.id}`} value={subFormInfo.label} checked = {props.optionalData[columnsInfo.id] !== undefined && props.optionalData[columnsInfo.id] === subFormInfo.label ? true : false} onChange = {props.changeQuestionInput} /> }
                      <label class="form-check-label" for={subFormInfo.id}>{subFormInfo.label}</label>
                    </div>
                  )}
                  </div>
                }
                {columnsInfo.type === "dropdown"  && <Input type ="select" className="form-control" value = {props.optionalData[columnsInfo.id] !== undefined  ? props.optionalData[columnsInfo.id] : ""} name={`${props.parentElementId}_optionalData__${props.subparentElementId}__${columnsInfo.id}`} onChange = {props.changeQuestionInput}>
                  {columnsInfo.subFormData.map((subFormInfo, subIndex) => 
                      <option value={subFormInfo.label} key = {subIndex} >{subFormInfo.label}</option>
                  )}
                </Input>}
                {(columnsInfo.type === "text" || columnsInfo.type === "number" || columnsInfo.type === "date") && 
                <input type = {columnsInfo.type} className="form-control" value = {props.optionalData[columnsInfo.id] !== undefined  ? props.optionalData[columnsInfo.id] : ""} name={`${props.parentElementId}_optionalData__${props.subparentElementId}__${columnsInfo.id}`} onChange = {props.changeQuestionInput} />}
              </div>
            </div>
        )}
          
    </div>
  </div>)
}

class PreviewTemplatePageForm extends Component {
  
  constructor(props){
    super(props);   
    this.state = {
      buttonProcessing: false,
      rowIndex: '',
      dataTableItem: [],
      formFieldRemarks: {},
      formFieldValueRemarks: {},
      mediaFileData: {},
      inspectionId: "",
      modal: false,
      actionData: {}
    };
    this.submitHandler = this.submitHandler.bind(this);
    this.handleActionInput = this.handleActionInput.bind(this);
  }
  componentDidMount() {   
    let formFiled = {};
    let formErrors = {};
    this.setState({inspectionId: this.props.inspectionId, mediaFileData: this.props.previousUploadedFile});
    for(const formFieldDetails of this.props.templateField.entries()){
      if(formFieldDetails.element === "Header" || formFieldDetails.element === "Paragraph" || formFieldDetails.element === "LineBreak" || formFieldDetails.element === "Label")
        continue;
      if(formFieldDetails.required)
        formErrors[formFieldDetails.id] = "";
      formFiled[formFieldDetails.id] = "";
    };
    this.props.createFormFieldName(formFiled, formErrors);
    
  }

  /* Update Counter On Each */
  componentDidUpdate(){
    
    this.props.updateQuestionCountval(countQuestion);
  }
  /* Change Input Field*/
  changeHandle = event => {
    this.props.updateFormFieldValue(event.target.name, event.target.value);
  }

  /*Browse File to Upload*/

  changeFileHandle = event => {
    const targetFile = event.target.files[0];
    if(targetFile.type !== "image/png" && targetFile.type !== "image/jpeg" && targetFile.type !== "image/jpg" && targetFile.type !== "image/svg") {
      toast.error("Images allowed only");
      return false;
    }
    //const targetFieldName = event.target.name;       
    var reader = new FileReader();
    const objProps = this;
    let mediaFileData = this.state.mediaFileData;
    const inputFieldId = event.target.dataset.inputid;
    let mediaFileDataArray = mediaFileData[event.target.dataset.inputid] ? mediaFileData[event.target.dataset.inputid] : []
    reader.onload = function(e) {
      mediaFileDataArray.push(e.target.result);      
      mediaFileData[inputFieldId] = mediaFileDataArray;
      objProps.setState({mediaFileData: mediaFileData});
      objProps.props.updateMediaFile(inputFieldId, targetFile, e.target.result);
    }
    reader.readAsDataURL(targetFile);
    
  }

  /*Handle Remarks Data*/
  remarkChangeEventHandle = event =>  {
    let formFieldValueRemarks = this.state.formFieldValueRemarks;
    let fieldName = event.target.name.split('remarks__');
    formFieldValueRemarks[fieldName[1]] = event.target.value;
    this.setState({formFieldValueRemarks: formFieldValueRemarks});
    this.props.updateRemarks(fieldName[1], event.target.value);
    
  }

  

  /*Show Remarks Form*/
  remarkEventHandle = event => {     	
    let formFieldRemarks = this.state.formFieldRemarks;
    formFieldRemarks[event.target.dataset.inputid] = true;
    this.setState({formFieldRemarks: formFieldRemarks});
  }
  /*Save Remarks Form*/
  remarkSaveEventHandle = event =>  {
    let formFieldRemarks = this.state.formFieldRemarks;
    formFieldRemarks[event.target.dataset.inputid] = false;
    this.setState({formFieldRemarks: formFieldRemarks});
    let formFieldValueRemarks = this.state.formFieldValueRemarks;
    this.props.updateRemarks(event.target.dataset.inputid, formFieldValueRemarks[event.target.dataset.inputid]);
    
    
  }

  /*Hide Remarks Form*/
  cancelRemarkEventHandle = event => {
    let formFieldRemarks = this.state.formFieldRemarks;
    formFieldRemarks[event.target.dataset.inputid] = false;
    //let formFieldValueRemarks = this.state.formFieldValueRemarks;
    //formFieldValueRemarks[event.target.dataset.inputid] = "";
    this.setState({formFieldRemarks: formFieldRemarks});

    //this.props.updateRemarks(event.target.dataset.inputid, "");
  }

  deleteInspectionImage = event => {
    
    let mediaFileData = this.state.mediaFileData;
    const inputFieldId = event.target.dataset.inputid;
    let mediaFileDataArray = mediaFileData[event.target.dataset.inputid] ? mediaFileData[event.target.dataset.inputid] : [];
    mediaFileDataArray.splice(parseInt(event.target.dataset.currentindex), 1);
    mediaFileData[inputFieldId] = mediaFileDataArray;
    this.setState({mediaFileData: mediaFileData});
    this.props.handleRemoveMediaFile(event.target.dataset.inputid, parseInt(event.target.dataset.currentindex));

  }


  /*Action Handle Request*/

  actionEventHandle = event => {
    let actionFormData = this.state.actionData;
    
    let questionActionInfo = this.props.actionValue[event.target.dataset.inputid] ? this.props.actionValue[event.target.dataset.inputid] : {};
    actionFormData['questionId'] = event.target.dataset.inputid;
    actionFormData['action_description'] = questionActionInfo.description || "";
    actionFormData['employee_id'] = questionActionInfo.employeeId || "";
    actionFormData['priority_input'] = questionActionInfo.priority || "";
    actionFormData['due_date'] = questionActionInfo.dueDate || "";
    this.setState({
      modal: true, 
      actionData: actionFormData    
    });
  }

  toggle = () => {
    this.setState({
      modal: !this.state.modal,     
    });
  }

  submitHandler (event) {
    event.preventDefault();
    let actionFormData = this.state.actionData;
    actionFormData.priority_input = actionFormData.priority_input ? actionFormData.priority_input : 1;
    let formData = {description: actionFormData.action_description, employeeId: actionFormData.employee_id, priority: actionFormData.priority_input, dueDate: actionFormData.due_date};
    this.props.updateAction(actionFormData.questionId, formData);
    this.setState({
      modal: false,     
    });
  }

  handleActionInput = event => {
    let actionFormData = this.state.actionData
    actionFormData[event.target.name] = event.target.value;
    this.setState({actionData: actionFormData});
  }
  
  render() {
    const formFiled = this.props.templateField;
    countQuestion = this.props.countQuestion;
   
    return (
      <>
      <div className="inspection-form-section">
         {formFiled.map((formFieldDetails, index) =>
            formFieldDetails === null || formFieldDetails === undefined ? "" : 
            <FieldLayout key={index} indexItem = {index} formFieldDetails={formFieldDetails} formFieldName = {this.props.createFormFieldName} 
            mediaFileData={this.props.previousUploadedFile[formFieldDetails.id] ? this.props.previousUploadedFile[formFieldDetails.id] : []} 
            formValue = {this.props.formField[formFieldDetails.id]}
            optionalData = {this.props.formField[formFieldDetails.id+'_optionalData'] || ""} 
            remarksValue={this.props.remarksValue[formFieldDetails.id] ? this.props.remarksValue[formFieldDetails.id] : ""} onchangeEvent={this.changeHandle} 
            onchangeFileEvent={this.changeFileHandle} remarkSaveEvent={this.remarkSaveEventHandle} 
            remarkChangeEvent={this.remarkChangeEventHandle} remarkEvent={this.remarkEventHandle} 
            formFieldRemarks={this.state.formFieldRemarks} cancelRemarkEvent={this.cancelRemarkEventHandle} 
            clickMoreTabularRows = {this.props.clickMoreTabularRows}
            clickRemoveTabularRows = {this.props.clickRemoveTabularRows}
            onchangeEventWithMultiDimenssional = {this.props.onchangeEventWithMultiDimenssional}
            onchangeQuestionInput = {this.props.onchangeQuestionInput}
            optionalDataInput = {this.props.optionalDataInput}
            deleteImage= {this.deleteInspectionImage.bind(this)} disabledModule={this.props.disabledModule} actionEvent={this.actionEventHandle} actionValue={this.props.actionValue[formFieldDetails.id] ? this.props.actionValue[formFieldDetails.id] : ""}  />
          )}
          
      </div>
      
      </>
    );
  }
}

